.game{
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
  }

.wrapper{
    display: flex;
    border: 3px solid black;
    background-color: #f8f8f8e1;
}

.cell{
    width: 3rem;
    height: 3rem;
    border: 1px solid lightgrey;
}

.cell-current{
  background-color: rgb(179, 179, 179);
}

.cell-empty {

}

.cell-wall{
  background-color: black;
}

.button{
  width: 50px;
  height: 50px;
  border-radius: 10px;
  cursor: pointer;
  border: 1px solid lightgrey;
}
.button:hover{
  background-color: lightgrey;
}
  